.setting{
    margin-top: 5vh;
}

.containerSetting {
    width: 100%;
    margin-top: 20vh;
}
  
.leftSide, .rightSide {
    display: inline-block;
    width: 50%;
    height: 200px;
    font-size: 30px;
    color: black
}
  
.leftSide {
    background-color: rgb(186, 186, 217);
    border-radius: 15%;
}

.leftSide:hover{
    background-color: rgb(255, 242, 0);
    font-weight: bold;
    font-size: 40px;
}
  
.rightSide {
    background-color: rgb(210, 255, 210);
    border-radius: 15%;
}

.rightSide:hover{
    background-color: rgb(255, 242, 0);
    font-weight: bold;
}
  
.bot{
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.fontLabel{
    font-weight: bold;
    font-size: larger;
}

.selected{
    background-color: rgb(241, 255, 91);
    width: 100px;
    color: black;
    font-weight: bold;
}

.unselected{
    width: 100px;
    background-color: aliceblue;
    color: black;
    font-weight: bold;
}

.unselected:hover{
    background-color: rgb(241, 255, 91);
}

.btns{
    margin-left: 10%;
}

.colors{
    font-weight: bold;
}

.color{
    width:14%;
    background-color: aliceblue;
    color: black;
}

.red {
    background-color: red;
    width:14%;
}

.orange {
    background-color: rgb(241, 105, 0);
    width:14%;
}

.yellow {
    background-color: rgb(173, 170, 0);
    width:14%;
}


.green{
    background-color: green;
    width:14%;
}


.blue{
    background-color: rgb(3, 3, 191);
    width:14%;
}

.black{
    background-color: black;
    width:14%;
}


