*{
    margin: 0;
    box-sizing: border-box;
    background-color: rgb(32, 27, 27);
    color: white;
}

html.dark-mode {
    background-color: #000000;
}
  

.cover{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.title{
    font-weight: bold;
    font-size: 30px;
}

.buttons{
    margin-top: 20vh;
    display: flex;
    flex-direction: column;
}

.link{
    font-size: 30px;
    padding: 0px;
    border: 1px solid white;
    background-color: rgb(87, 87, 83);
    margin-top: 5px;
}

.link:hover {
    background-color: #9e8c00;
    color: black;
  }


.animated-text {
    background-image: -webkit-linear-gradient(DarkSalmon 50%, LightYellow 50%);
    background-size: 100% 50px;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-animation: stripes 2s linear infinite;
    animation: stripes 2s linear infinite;
  }
  @-webkit-keyframes stripes {
    100% {
      background-position: 0 -50px;
    }
  }
  @keyframes stripes {
    100% {
      background-position: 0 -50px;
    }
}



.helloWorld{
    font-family: "Incosolata", Consolas, monospace;
    font-size: 22px;
    color: #4cc9d7;
    margin-top:20vh;
    padding:0;
}
.typewriter{
    position: relative;
}
.typewriter::after{
    content: '|';
    position: absolute;
    right: 0;
    color: white;
    background-color: rgb(32, 27, 27);
    width: 100%;
    animation: typing 3s steps(7) alternate infinite,
                line 1s infinite;
}
@keyframes typing{
    to{
        width: 0%;
    }
}
@keyframes line {
    50%{
        color: transparent;
    }
}
