#form_login {
   height:500px;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;  
   font-weight: bold;

}

.loginTitle{
    margin-bottom: 20px;
}

.loginBtn{
    padding: 2px;
    width: 100px;
    background-color: white;
    color: black;
    margin-top: 10px;
}

.loginBtn:hover{
    background-color: rgb(0, 224, 212);
}

.input{
    margin-top: 10px;
    width: 230px;
    /* background-color: aqua; */
    display: flex;
}

.bi{
    margin-top: 10vh;
}

.label{
    margin-right: 5px;
    margin: auto;
    padding: auto;
    flex: 3;
}

.enter{
    height: 25px;
    flex: 5;
}

.registerLink{
    margin-top: 20px;
    color: rgb(0, 208, 255);
    font-size: 13px;
}

.registerLink:hover{
    color: rgb(218, 218, 0);
}

.txt{
    width: 100%;
}